:root {
  background-color: #bababa;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.settings,
.profile,
.singup,
.login {
  width: 360px;
  position: absolute;
  padding: 30px;
  border-radius: 3px;
  -webkit-box-shadow: 1px 1px 8px 3px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 1px 1px 8px 3px rgba(34, 60, 80, 0.2);
  box-shadow: 1px 1px 8px 3px rgba(34, 60, 80, 0.2);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;

  &__title {
    opacity: 0.5;
    text-align: center;
    margin-bottom: 15px;
  }

  &__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  &__inputs {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .link {
    align-items: center;
    width: 100%;
    margin-top: 2px;
    display: flex;
    justify-content: space-between;
  }

  &__options {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    margin-bottom: 70px;
  }

  &__name {
    display: flex;
  }
}

.profile {
  width: min-content;

  &__data {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &--avatar {
    width: 40%;
    margin-top: 24px;
    border-radius: 100%;
    box-shadow: 0 2px 21px 4px rgba(0, 0, 0, 0.26);
  }

  &__fullname {
    display: flex;
    margin: auto;
    justify-content: center;
    margin-bottom: 10px;
  }

  &__fullname--name,
  &__fullname--surname {
    margin: 0 3px;
    font-size: 24px;
  }

  &__actions {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.m-10 {
  margin: 10px 0;
}
.my-5 {
  margin: 0 5px 0 0;
}

.bg-red {
  background-color: #ee5050;
  color: white;
}

.bg-grey {
  background-color: #d9d9d9;
  color: #000000;
}

.alert {
  color: #ee5050;
  font-size: 16px;
  opacity: 1;
  transition: opacity ease-in .3s;
}

.hide{
  opacity: 0;
}

@media (max-width: 600px) {
  .settings,
  .profile,
  .singup,
  .login {
    width: 300px;
    margin: 5% 0;
    padding: 23px;
  }
}

table{
  border-collapse: collapse;
  margin-bottom: 10px;
}

th,td{
  border: 1px solid rgba(0, 0, 0, 0.26);
  padding: 5px;
}

tbody{
  overflow-y: scroll;
}